
import { NextFunction, Request, Response, Router } from "express";
import { BaseRoute } from "./route";


export class BrowseRoute extends BaseRoute {
    protected recipes: any;

    public static create(router: Router) {

        //add home page route
        router.get("/browse", (req: Request, res: Response, next: NextFunction) => {
            new BrowseRoute().browse(req, res, next);
        });
    }

    constructor() {
        super();
    }

    public browse(req: Request, res: Response, next: NextFunction) {
        //set custom title
        this.title = "Browse Recipes";

        //Extracts test data from public folder to fill display
        const a = require("../../public/data/test1.json");
        const b = require("../../public/data/test2.json");
        
        //set message
        let options: Object = {
            "message": "Browse Page",
            "recipes": [a, b],
        };

        //render template
        this.render(req, res, "browse", options);
    }
}
